* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Nunito', sans-serif;
}

 body {
  background-image: url(../public/beautiful-milky-way-night-sky.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}
 


select {
  width: 100px;
  padding: 3px;
  border-radius: 1.5px;
  border: 0 ;
  color: #fff;
  background-color: #041f2c;

  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;

  background-image: url("https://popupfilmresidency.org/wp-content/uploads/2019/05/white-down-arrow-png-2.png");
    background-size: 1em;
    background-position: calc(100% - 1.3em) center;
    background-repeat: no-repeat;
}


input {
  width: 100px;
  padding: 3px;
  border-radius: 1.5px;
  color: #fff;
  border-color:#041f2c ;
  border-width: 3px;
  background-color: #5a5b5b;
  border-style:solid;
}

@media screen and (min-width: 375px) {
  input {
    width: 100px;
    padding: 3px;
    border-radius: 1.5px;
  }

  select {
    width: 100px;
    padding: 3px;
    border-radius: 1.5px;
  }
}

@media screen and (min-width: 800px) {
  input {
    width: 150px;
    padding: 6px;
    border-radius: 3px;
  }

  select  {
    width: 150px;
    padding: 6px;
    border-radius: 3px;
  }
}

@media screen and (min-width: 1038px) {
  input {
    width: 300px;
    padding: 12px;
    border-radius: 5px;
  }

  select {
    width: 300px;
    padding: 12px;
    border-radius: 5px;
  }
}

