.app {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding-bottom: 10px;
}

.converter-wrapper {
  display: flex;
  flex-direction: row;
  gap: 10px;
}

@media screen and (min-width: 375px) {
  .app {
    gap: 20px;
  }

  .converter-wrapper {
    gap: 10px;
  }
}

@media screen and (min-width: 800px) {
  .app {
    gap: 40px;
  }

  .converter-wrapper {
    gap: 15px;
  }
}

@media screen and (min-width: 1038px) {
  .app {
    gap: 60px;
  }

  .converter-wrapper {
    gap: 20px;
  }
}
