.header_container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
}

.header_container_item {
  color: #fff;
  font-size: 0.5rem;
  text-transform: uppercase;
  font-weight: bold;
}

@media screen and (min-width: 375px) {
  .header_container_item {
    font-size: 1rem;
  }
}

@media screen and (min-width: 800px) {
  .header_container_item {
    font-size: 1.5rem;
  }
}

@media screen and (min-width: 1038px) {
  .header_container_item {
    font-size: 2rem;
  }
}



