.converter {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.converter-item-text {
  color: #fff; 
  font-size: 0.5rem;
  text-transform:lowercase;
  font-weight:lighter
}


@media screen and (min-width: 375px) {
  .converter-item-text {
    font-size: 0.5rem;
  }
}

@media screen and (min-width: 800px) {
  .converter-item-text {
    font-size: 0.75rem;
  }
}

@media screen and (min-width: 1038px) {
  .converter-item-text {
    font-size: 1rem;
  }
}
